import {  
    ModuleWithProviders, Component  
} from '@angular/core';  
import {  
    Routes,  
    RouterModule  
} from '@angular/router';  
import {  
    AppComponent  
} from './app.component';  
import {  
    ContactlistComponent  
} from './contactlist/contactlist.component';  
import {  
    ContactformComponent  
} from './contactform/contactform.component';  
import { ManufacturerComponent } from './manufacturer/manufacturer.component';
import { ManufacturerAddComponent } from './manufacturer-add/Manufacturer-add.component';
import {CustomerListComponent} from './customer-list/customer-list.component';
import { combineAll } from 'rxjs/operators';
import {CustomerAddformComponent} from './customer-addform/customer-addform.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {BillinglistComponent} from './billinglist/billinglist.component';
import { BillingViewComponent } from './billing-view/billing-view.component';
import {BillingUpdateComponent} from './billing-update/billing-update.component';

const appRoutes: Routes = [{  
    path: 'productDetails',  
    
    component: ContactlistComponent  
}, {  
    path: 'contactform',  
    component: ContactformComponent  
},{

    path:'manufacturer',
    component: ManufacturerComponent
},
{
    path:'manufacturerAdd',
    component:ManufacturerAddComponent
},
{
 path:'customerlist',
 component:CustomerListComponent    
},
{

    path:'customerAdd',
    component:CustomerAddformComponent    
},
{
path : '',
//pathMatch: 'full',  
component:DashboardComponent

},
{
    path:'billingList',
    component:BillinglistComponent
},
{
     path : 'billingView',
     component: BillingViewComponent

},
{
    path : 'billingUpdate/:bill/:code/:date/:name/:amount/:mobile',
    component: BillingUpdateComponent

}
];  
export const Routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);