import { Component, OnInit,ViewChild} from '@angular/core';
import { MatTableDataSource, MatSnackBar,MatPaginator,MatFormFieldModule, MatInputModule } from '@angular/material';
import { ContactService } from '../services/contact.service';
import { Global } from '../shared/Global';
import { IContact } from '../model/contact';
import { DBOperation } from '../shared/DBOperation';
import { ManufacturerAddComponent } from '../manufacturer-add/Manufacturer-add.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, } from '@angular/material';
import { ContactformComponent } from '../contactform/contactform.component';
@Component({
  selector: 'app-manufacturer',
  templateUrl: './manufacturer.component.html',
  styleUrls: ['./manufacturer.component.css']
})
export class ManufacturerComponent implements OnInit {

  dbops: DBOperation;
  modalTitle: string;
  modalBtnTitle: string;
  manufacturer: IContact;
  loadingState: boolean;

  @ViewChild(MatPaginator ,{ read: true,static: true}) paginator: MatPaginator;


  displayedColumns = ['name', 'code', 'action'];

  dataSource = new MatTableDataSource<IContact>();

  constructor(public snackBar: MatSnackBar,private _contactService: ContactService,private dialog: MatDialog) { }

  ngOnInit() {
    this.loadingState = true;
    this.manuList();
  }

  ngAfterViewInit(): void {
    
    this.dataSource.paginator = this.paginator;
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(ManufacturerAddComponent, {
      width: '1500px',
      data: { dbops: this.dbops, modalTitle: this.modalTitle, modalBtnTitle: this.modalBtnTitle, manufacturer: this.manufacturer }
    });
 


    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result === 'success') {
        this.loadingState = true;
        this.manuList();
        switch (this.dbops) {
          case DBOperation.create:
            this.showMessage('Data successfully added.');
            break;
          case DBOperation.update:
            this.showMessage('Data successfully updated.');
            break;
          case DBOperation.delete:
            this.showMessage('Data successfully deleted.');
            break;
        }
      } else if (result === 'error') {
        this.showMessage('There is some issue in saving records, please contact to system administrator!');
      } else {
       // this.showMessage('Please try again, something went wrong');
      }
    });
    
  }
  manuList(): void {
    this._contactService.getManuList(Global.BASE_USER_ENDPOINT + 'getAllManu')
      .subscribe(manufacturer => {
        
        this.dataSource.data = manufacturer;
      });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

  editManu(id: number) {
    this.dbops = DBOperation.update;
    this.modalTitle = 'Edit Manufacture';
    this.modalBtnTitle = 'Update';
    this.manufacturer = this.dataSource.data.filter(x => x.id === id)[0];
    this.openDialog();
  }
  deleteManu(id: number){
    this.dbops = DBOperation.delete;
    this.modalTitle = 'Do you want to Delete? ';
    this.modalBtnTitle = 'Delete';
    this.manufacturer = this.dataSource.data.filter(x => x.id === id)[0];
    this.openDialog();
  }
  addManufacturer(){
    this.dbops = DBOperation.create;
   this.modalTitle='Add new ';
   this.modalBtnTitle = 'Save';
   this.openDialog();

  }
  showMessage(msg: string) {  
    this.snackBar.open(msg, '', {
      duration: 3000
    });
  }
}
