import { Component, OnInit,Inject } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { FormBuilder, Validators } from '@angular/forms';
import { DBOperation } from '../shared/DBOperation';
import { Global } from '../shared/Global';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, AUTOCOMPLETE_PANEL_HEIGHT } from '@angular/material';
import { ContactService } from '../services/contact.service';
import { IContact } from '../model/contact';
import {ManufacturerComponent} from '../manufacturer/manufacturer.component';
@Component({
  selector: 'app-manufacturer-add',
  templateUrl: './manufacturer-add.component.html',
  styleUrls: ['./manufacturer-add.component.css']
})
export class ManufacturerAddComponent implements OnInit {

  manuFrm : FormGroup;

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,private fb: FormBuilder,private _contactService: ContactService,public dialogRef: MatDialogRef<ManufacturerComponent>) { }

  ngOnInit() {


    this.manuFrm = this.fb.group({

      id: [''],
      name: ['', [Validators.required, Validators.maxLength(50)]],
      code: ['', [Validators.required]],
    });

    //fetch data into Edit
    if (this.data.dbops === DBOperation.create) {
      manuFrm : FormGroup;
      this.manuFrm.reset();
    } else {
      this.manuFrm.setValue(this.data.manufacturer);
    }

    this.SetControlsState(this.data.dbops === DBOperation.delete ? false : true);

  }



  onSubmit(formData: any) {
    const manufData = this.mapDateData(formData.value);
    switch (this.data.dbops) {
      case DBOperation.create:
      formData.value.id=5;
        this._contactService.addManufacturer(Global.BASE_USER_ENDPOINT_MANU + 'addManufacturer', manufData).subscribe(
          data => {
            // Success
            if (data.message) {
              this.dialogRef.close('success');
            } else {
              this.dialogRef.close('error');
            }
          },
          error => {
            this.dialogRef.close('error');
          }
        );
        break;
      case DBOperation.update:
        this._contactService.updateManufacturer(Global.BASE_USER_ENDPOINT_MANU + 'updateManufacturer', manufData.id, manufData).subscribe(
          data => {
            // Success
            if (data.message) {
              this.dialogRef.close('success');
            } else {
              this.dialogRef.close('error');
            }
          },
          error => {
            this.dialogRef.close('error');
          }
        );
        break;
      case DBOperation.delete:
        this._contactService.deleteManufacturer(Global.BASE_USER_ENDPOINT_MANU + 'deleteManufacturer', manufData.id).subscribe(
          data => {
            // Success
            if (data.message) {
              this.dialogRef.close('success');
            } else {
              this.dialogRef.close('error');
            }
          },
          error => {
            this.dialogRef.close('error');
          }
        );
        break;
    }
  }
  mapDateData(manufac: IContact): IContact {
    //contact.birth = new Date(contact.birth).toISOString();
    return manufac;
  }

  SetControlsState(isEnable: boolean) {
    isEnable ? this.manuFrm.enable() : this.manuFrm.disable();
  }

}
