import { Component, OnInit,ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ContactService } from '../services/contact.service';
import { Global } from '../shared/Global';
import { MatTableDataSource, MatSnackBar,MatPaginator,MatFormFieldModule, MatInputModule } from '@angular/material';
import { IContact } from '../model/contact';
import { DBOperation } from '../shared/DBOperation';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, } from '@angular/material';
import {CustomerAddformComponent} from '../customer-addform/customer-addform.component';


@Component({
  selector: 'app-customer-list',
  templateUrl: './customer-list.component.html',
  styleUrls: ['./customer-list.component.css']
})
export class CustomerListComponent implements OnInit {
  dataSource = new MatTableDataSource<IContact>();
  customers: IContact[];
  customer: IContact;
  dbops: DBOperation;
  modalTitle: string;
  modalBtnTitle: string;
  loadingState: boolean;
  private sorted = false;


  @ViewChild(MatPaginator ,{ read: true,static: true}) paginator: MatPaginator;


  displayedColumns = ['name', 'code', 'address', 'mobile', 'action'];

  constructor( public snackBar: MatSnackBar, private _contactService: ContactService,private dialog: MatDialog) { }

  ngOnInit() {
    this.customerList();
  }
  ngAfterViewInit(): void {
    
    this.dataSource.paginator = this.paginator;
  }
  openDialog(): void {
    const dialogRef = this.dialog.open(CustomerAddformComponent, {
      width: '500px',
      data: { dbops: this.dbops, modalTitle: this.modalTitle, modalBtnTitle: this.modalBtnTitle, contact: this.customer }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result === 'success') {
        this.loadingState = true;
        this.customerList();
        switch (this.dbops) {
          case DBOperation.create:
            this.showMessage('Data successfully added.');
            break;
          case DBOperation.update:
            this.showMessage('Data successfully updated.');
            break;
          case DBOperation.delete:
            this.showMessage('Data successfully deleted.');
            break;
        }
      } else if (result === 'error') {
        this.showMessage('There is some issue in saving records, please contact to system administrator!');
      } else {
       // this.showMessage('Please try again, something went wrong');
      }
    });
  }
  customerList() : void{

 this._contactService.getCustomerList(Global.BASE_USER_ENDPOINT_CUST+'getCustomerList')
 .subscribe(customers => {
  this.dataSource.data = customers;
});


  }

  deleteCustomer(id:number){
    this.dbops = DBOperation.delete;
    this.modalTitle = 'Delete Customer';
    this.modalBtnTitle = 'Delete';
    this.customer = this.dataSource.data.filter(x => x.id === id)[0];
    this.openDialog();
  }
  editCustomer(id:number){

    this.dbops = DBOperation.update;
    this.modalTitle = 'Edit Product';
    this.modalBtnTitle = 'Update';
    this.customer = this.dataSource.data.filter(x => x.id === id)[0];
    this.openDialog();

  }

  addCustomer(){
    this.dbops = DBOperation.create;
    this.modalTitle = 'Add Customer';
    this.modalBtnTitle = 'Save';
    this.openDialog();
  }
  showMessage(msg: string) {  
    this.snackBar.open(msg, '', {
      duration: 3000
    });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

}
