import { Component, OnInit ,Inject} from '@angular/core';
import { FormGroup,Validators,FormBuilder } from '@angular/forms';
import { IContact } from '../model/contact';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, AUTOCOMPLETE_PANEL_HEIGHT } from '@angular/material';
import { DBOperation } from '../shared/DBOperation';
import { Global } from '../shared/Global';
import { ContactService } from '../services/contact.service';
import {CustomerListComponent} from '../customer-list/customer-list.component';

@Component({
  selector: 'app-customer-addform',
  templateUrl: './customer-addform.component.html',
  styleUrls: ['./customer-addform.component.css']
})
export class CustomerAddformComponent implements OnInit {

  dbops: DBOperation;
  modalBtnTitle: String;
  customerFrm: FormGroup;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,private fb: FormBuilder,private _contactService: ContactService,public dialogRef: MatDialogRef<CustomerListComponent>) { }

  ngOnInit() {


    this.customerFrm = this.fb.group({

      id: [''],
      name: ['', [Validators.required, Validators.maxLength(50)]],
      code: ['', [Validators.required]],
      address: ['', [Validators.required]],
      mobile: ['', [Validators.required]],
    });
//fetch data into edit
if (this.data.dbops === DBOperation.create) {
  customerFrm : FormGroup;
  this.customerFrm.reset();
} else {
  this.customerFrm.setValue(this.data.contact);
}

this.SetControlsState(this.data.dbops === DBOperation.delete ? false : true);
  }

  onSubmit(formData: any){
      
const customerData = this.mapDateData(formData.value);

switch (this.data.dbops) {
  
  case DBOperation.create:
  formData.value.id=5;
    this._contactService.addCustomer(Global.BASE_USER_ENDPOINT_CUST + 'addCustomer', customerData).subscribe(
      data => {
        // Success
        if (data.message) {
          this.dialogRef.close('success');
        } else {
          this.dialogRef.close('error');
        }
      },
      error => {
        this.dialogRef.close('error');
      }
    );
    break;
  case DBOperation.update:
    this._contactService.updateCustomer(Global.BASE_USER_ENDPOINT_CUST + 'updateCustomer', customerData.id, customerData).subscribe(
      data => {
        // Success
        if (data.message) {
          this.dialogRef.close('success');
        } else {
          this.dialogRef.close('error');
        }
      },
      error => {
        this.dialogRef.close('error');
      }
    );
    break;
  case DBOperation.delete:
    this._contactService.deleteCustomer(Global.BASE_USER_ENDPOINT_CUST + 'deleteCustomer', customerData.id).subscribe(
      data => {
        // Success
        if (data.message) {
          this.dialogRef.close('success');
        } else {
          this.dialogRef.close('error');
        }
      },
      error => {
        this.dialogRef.close('error');
      }
    );
    break;
}
  }
  mapDateData(customerData: IContact): IContact{
   return customerData;

  }
  SetControlsState(isEnable: boolean) {
    isEnable ? this.customerFrm.enable() : this.customerFrm.disable();
  }

}
